import React, { Component } from 'react';

//css
import 'normalize.css/normalize.css';
import './styles/App.css';

import CirclesLayer from './components/CirclesLayer';
import Logo from './components/Logo';
import Subscribe from './components/Subscribe';

import { Power1, TimelineMax } from 'gsap';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      logoActive: false,
      subscribeActive: false,
      thankYou: false,
    };
    this.onCompleteLogo = this.onCompleteLogo.bind(this);
    this.onCompleteInitialCircles = this.onCompleteInitialCircles.bind(this);
    this.onCompleteSubscribe = this.onCompleteSubscribe.bind(this);
    this.onRegister = this.onRegister.bind(this);
  }

  onCompleteInitialCircles(id, initialCircleNumber) {
    if (parseInt(id.split('_')[2], 10) === initialCircleNumber) {
      this.setState({
        logoActive: true,
      });
    }
  }

  onCompleteLogo() {
    console.log('logo done');
    this.setState({
      subscribeActive: true,
    });
  }

  onCompleteSubscribe() {
    console.log('subscribe done');
  }

  thankyouAnim() {
    const tl = new TimelineMax({
      repeat: 0,
      onComplete: this.props.onComplete,
    });
    tl.from('.subscribe-thankyou', 1.3, {
      ease: Power1.easeOut,
      opacity: 0,
    });
  }

  onRegister(email) {
    console.log('e2:', email);
    this.subscribeServer(email);
    this.setState(
      {
        subscribeActive: false,
        thankYou: true,
      },
      () => {
        this.thankyouAnim();
      }
    );
  }

  subscribeServer(email) {
    let headers = new Headers();
    console.log('e:', email);
    headers.append('Accept', 'application/json');
    headers.append('Content-Type', 'application/json');
    let url =
      'https://wt-3b8a83453e0b25811c2f9df926320e92-0.run.webtask.io/rideo_comingsoon/subscribe';
    let opts = {
      email_address: email,
    };
    console.log(JSON.stringify(opts));
    fetch(url, {
      method: 'post',
      headers: headers,
      body: JSON.stringify(opts),
    }).then(response => {
      console.log(response);
    });
  }

  render() {
    return (
      <div className="App">
        <CirclesLayer onComplete={this.onCompleteInitialCircles} />
        {this.state.logoActive && <Logo onComplete={this.onCompleteLogo} />}
        {this.state.subscribeActive && (
          <Subscribe
            onComplete={this.onCompleteSubscribe}
            onRegister={this.onRegister}
          />
        )}

        {this.state.thankYou && (
          <div className="subscribe-thankyou">
            <h3>
              {'Thank you for your interest.'}
              <br />
              {'We’ll be in touch. '}
            </h3>
          </div>
        )}
      </div>
    );
  }
}

export default App;



