import React, { Component } from 'react';
import { Power1, TimelineMax, Elastic } from 'gsap';

import Input from './Input';

class Subscribe extends Component {
  constructor(props) {
    super(props);
    this.state = {
      buttonActive: false,
      email: '',
      formReady: false,
      thankYou: false,
    };
    this.subscribeClick = this.subscribeClick.bind(this);
    this.emailChange = this.emailChange.bind(this);
    this.register = this.register.bind(this);
  }

  componentDidMount() {
    this.subscribeAnimation();
  }

  emailChange({ value }) {
    this.setState({
      email: value,
    });

    if (
      value.length > 4 &&
      value.indexOf('@') > -1 &&
      value.indexOf('.') > -1
    ) {
      this.setState({
        formReady: true,
      });
    } else {
      this.setState({
        formReady: false,
      });
    }
  }

  register() {
    this.subscribeAnimationOut();
    let email = this.state.email;
    console.log('e3:', email);
    setTimeout(() => {
      this.setState({
        formReady: false,
        email: '',
        buttonActive: false,
      });
      this.props.onRegister(email);
    }, 500);
  }

  subscribeClick() {
    if (!this.state.buttonActive) {
      this.setState(
        {
          buttonActive: true,
        },
        () => {
          this.subscribeInputAnimationIn();
        }
      );
    } else {
      this.subscribeInputAnimationOut();
      setTimeout(() => {
        this.setState({
          buttonActive: false,
        });
      }, 500);
    }
  }

  subscribeAnimation() {
    const tl = new TimelineMax({
      repeat: 0,
      onComplete: this.props.onComplete,
    });
    tl.from('.subscribe-container', 1.3, {
      ease: Power1.easeOut,
      opacity: 0,
    });
  }

  subscribeAnimationOut() {
    const tl = new TimelineMax({
      repeat: 0,
    });
    tl.to('.subscribe-container', 0.8, {
      ease: Power1.easeOut,
      opacity: 0,
    });
  }

  subscribeInputAnimationIn() {
    const tl = new TimelineMax({
      repeat: 0,
    });
    tl.from('.subscribe-input', 0.9, {
      ease: Elastic.easeOut.config(1, 0.5),
      y: -30,
      opacity: 0,
    });
  }

  subscribeInputAnimationOut() {
    const tl = new TimelineMax({
      repeat: 0,
    });
    tl.to('.subscribe-input', 0.5, {
      ease: Power1.easeOut,
      y: 50,
      opacity: 0,
    });
  }

  render() {
    return (
      <div className="subscribe-container">
        {this.state.buttonActive && (
          <div className="subscribe-input">
            <Input
              type="text"
              name="email"
              className="subscribe_input"
              placeholder="email"
              autoComplete={'off'}
              pattern={'[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{2,3}$'}
              value={this.state.email}
              onChange={this.emailChange}
            />
            <button
              className="submitButton"
              disabled={!this.state.formReady}
              onClick={this.register}
            >
              SUBMIT
            </button>
          </div>
        )}

        <h1 className="invite-incentive-title">
          Rideo is for ride-sharers and travelers.
        </h1>
        <h2 className="invite-incentive">
          Get exclusive access to the upcoming beta app and invitations for
          friends when you‘re among the first 5000 to signup.
        </h2>
        <button
          className={
            'button-subscribe ' +
            (this.state.buttonActive ? 'button-active' : '')
          }
          onClick={this.subscribeClick}
        >
          get notified
        </button>
      </div>
    );
  }
}

export default Subscribe;



